<template>
  <div class="bookmall">
    <div class="bookmall-head clearfix">
      <div class="fl title-logo"></div>
      <div class="fl bookmall-bt">
        <div
          class="bookmall-nan"
          v-for="(item,index) in sexTypeData"
          :key="index"
          :class=" [item.isActive ? 'active':'']"
          @click="changeType(item,index)"
        >{{item.title}}</div>
      </div>
      <div class="fr icon-location">
        <span class="iconfont icon-shujia head-icons" @click="toggleTabBar('Bookshelf')"></span>
        <span class="iconfont icon-wodedangxuan head-icons" @click="toggleTabBar('My')"></span>
      </div>
    </div>
    <!-- 搜索 -->
    <div class="bookmall-search">
      <van-search placeholder="请输入搜索关键词" @focus="toggleTabBar('Search')" />
    </div>
    <!-- 轮播图 -->
    <van-swipe class="my-swipe-box" indicator-color="white" height="100px" autoplay="3000">
      <van-swipe-item v-for="(item,index) in hostData[type]" :key="index">
        <img v-lazy="item" class="auto-img" />
      </van-swipe-item>
    </van-swipe>

    <div class="clearfix bookmall-box">
      <div class="fl bookmall-box-icon" @click="toggleTabBar('Bookshelf')">
        <div class="icon-bg">
          <svg class="icon icon-img" aria-hidden="true">
            <use xlink:href="#icon-shujia1" />
          </svg>
        </div>
        <div class="icon-titles">书架</div>
      </div>
      <div class="fl bookmall-box-icon" @click="toggleTabBar('Ranking')">
        <div class="icon-bg">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-paihangbang" />
          </svg>
        </div>
        <div class="icon-titles">排行</div>
      </div>
      <div class="fl bookmall-box-icon" @click="toggleTabBar('My')">
        <div class="icon-bg">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-zhifu" />
          </svg>
        </div>
        <div class="icon-titles">支付</div>
      </div>
    </div>

    <div class="bookmall-box-histly clearfix">
      <div class="fl bookmall-box-read">最近阅读</div>
      <div class="fl">|</div>
      <div class="fl bookmall-box-not">{{bookTitle}}</div>
    </div>

    <div class="bookmall-item">
      <div class="bookmall-item-book">
        <div class="bookmall-item-headline">{{title}}热门</div>
        <div
          class="clearfix bookmall-books"
          v-for="(item,index) in lastdata"
          :key="index"
          @click="getBookDetial(item._id)"
        >
          <div class="fl bookmall-item-img">
            <img class="auto-img" :src="item.cover" alt />
          </div>
          <div class="fr book-title-box">
            <div class="bookmall-item-title">{{item.title}}</div>
            <div class="bookmall-item-content">{{item.shortIntro}}</div>
            <div class="bookmall-item-bookmask">
              <div class="fl">
                <div class="fl">{{item.minorCate}}</div>
                <div class="fl">|</div>
                <div class="fl">
                  <div class="bookmall-item-moods fl">{{item.latelyFollower}}</div>
                  <div class="fl">人气</div>
                </div>
              </div>
              <div class="item-label fr">{{item.majorCate}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/bookmall.less";
export default {
  data() {
    return {
      userName: 0,
      sexTypeData: [
        {
          title: "男生",
          name: "male",
          isActive: true,
        },
        {
          title: "女生",
          name: "female",
          isActive: false,
        },
      ],
      hostData: {
        female: [
          "https://plf-new.zhuishushenqi.com/management/images/20200710/600c145199ea4c3e826d5f9f07c7052e.jpg",
          "https://plf-new.zhuishushenqi.com/management/images/20200605/759ce296de5040458206a20ba88c3753.jpg",
          "https://plf-new.zhuishushenqi.com/management/images/20200731/0375b5f5658c4e52ae62acade23eebf5.jpg",
        ],
        male: [
          "https://plf-new.zhuishushenqi.com/management/images/20200731/9f32b76488004a6aad0bf16f60850a80.jpg",
          "https://plf-new.zhuishushenqi.com/management/images/20200717/4fa0c1d24edb421e88b4a1a1f7cc6506.jpg",
          "https://plf-new.zhuishushenqi.com/management/images/20200731/b4e8024d71c04943bbd0c08da90baf5f.jpg",
        ],
      },
      femaleData: [],
      maleData: [],
      lastdata: [],
      type: "male",
      title: "男生",
      value: "",
      input: null,
      bookTitle:''
    };
  },
  created() {
    this.getMaleBooks();
    this.getFemaleBooks();
    this.getHistoryBook();
  },
  methods: {
    changeType(item, index) {
      if (item.isActive) {
        return;
      }
      for (let i = 0; i < this.sexTypeData.length; i++) {
        if (this.sexTypeData[i].isActive) {
          this.sexTypeData[i].isActive = false;
          break;
        }
      }
      item.isActive = true;
      this.type = item.name;
      this.type == "male"
        ? (this.lastdata = this.maleData)
        : (this.lastdata = this.femaleData);
      this.title = item.title;
    },
    //路由跳转
    toggleTabBar(name) {
      this.$router.push({ name: name });
    },
    
    //获取男生热门书籍
    getMaleBooks() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/rank/54d42d92321052167dfb75e3",
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.ranking.books) {
            this.maleData = result.data.ranking.books;
            this.lastdata = this.maleData.splice(0, 15);
          }
          for (let i = 0; i < this.lastdata.length; i++) {
            this.lastdata[i].cover = unescape(this.lastdata[i].cover).slice(7);
          this.lastdata[i].latelyFollower = (this.lastdata[i].latelyFollower / 10000).toFixed(2);

          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //获取女生热门书籍
    getFemaleBooks() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/rank/54d43437d47d13ff21cad58b",
      })
        .then((result) => {
          this.$toast.clear();
          //
          if (result.data.ranking.books) {
            this.femaleData = result.data.ranking.books.splice(0, 15);
          }
          //栅格图片
          for (let i = 0; i < this.femaleData.length; i++) {
            this.femaleData[i].cover = unescape(this.femaleData[i].cover).slice(7);
          this.femaleData[i].latelyFollower = (this.femaleData[i].latelyFollower / 10000).toFixed(2);

          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    getBookDetial(id) {
      this.$router.push({ name: "Detail", query: { id: id } });
    },

    //获取最近阅读数据
    getBookData(id,title) {
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/book-info/" + id,
      }).then((result) => {
          if (result.data) {
            let books = result.data.title+"  "+title;
            this.bookTitle = books;
          }
        })
        .catch((err) => {
        });
    },
    // 设置最近阅读
    getHistoryBook(){
      let bookArr = this.$store.state.readHistory;
      if(bookArr.length == 0){
        this.bookTitle = '暂无阅读记录';
        return;
      }
      for(let i=0;i<bookArr.length;i++){
        if(bookArr.length-1 == i){
          this.getBookData(bookArr[i].bookId,bookArr[i].chapterTitle)
        }
      }
    }
  },
};
</script>